/*--------------------------------------------------------------
# Header 
--------------------------------------------------------------*/

.site-header {
  @include container;
}

.site-header--above {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  .site-header__nav {
    border-bottom: none;

    a {
      color: $color__background-body;
    }
  }
}

.site-header__nav {
  @include border-bottom;

  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: $base__space-unit;
  z-index: 9999;

  ul {
    @include reset-list;
    display: none;
  }

  li {
    position: relative;
    display: inline-block;
  }

  a {
    text-decoration: none;
    color: $color__brand;
  }

  .current_page_item > a {
    text-decoration: underline;
  }

  // menu items
  & > * * {
    padding: 0 0.5rem;
    text-transform: uppercase;
  }
}

.site-header__logo {
  height: 1.4 * $base__space-unit;
  min-width: 2.3 * $base__space-unit;
}

.site-header__title {
  @include sr-only;
}

/* collapsed */
.site-header__nav.toggled ul {
  display: unset;
}

// TODO: refactor breakpoints to use mixin
@media screen and (min-width: 768px) {
  .site-header__nav--closed {
    display: none;
  }
  .site-header__nav ul {
    display: unset;
  }
}
